//Header Image 
.picture-header {
    display: none;
    height: 400px;
    position: relative;
    @include dark-background( rgba( 0,0,0,.35 ) );
    padding: 0 1em;
    text-align: center;

    @media screen and (min-width: $query__small) {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}

.picture-header-mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 350px;
    position: relative;
    @include dark-background( rgba( 0,0,0,.35 ) );
    padding: 0 1em;
    text-align: center;

    @media screen and (min-width: $query__small) {
        display: none;
    }
}

//Header text
.title {
    font-size: 1.75rem;
    color: #fff;
    margin: 0;

    @media screen and (min-width: $query__small) {
        font-size: 3.5rem;
    }
}

.tagline {
    font-size: 1.1rem;
    color: #fff;
    margin: .25em 0 1em;
    word-spacing: .1em;

    @media screen and (min-width: $query__small) {
        font-size: 1.5rem;
    }
}

.header-title {
    font-size: 1.5rem;
    color: #fff;
    margin: 0;
    z-index: 1;
    position: relative;


    @media screen and (min-width: $query__small) {
        font-size: 2.65rem;
    }
}

.study-header-title {
    font-size: 1.25rem;
    color: #fff;
    margin: 0;
    z-index: 1;
    position: relative;
    @include header-uline( $color__site-green );

    @media screen and (min-width: $query__small) {
        font-size: 2.25rem;
    }
}

.header-tagline {
    font-size: 1.1rem;
    color: #fff;
    margin: .25em 0 1em;
    word-spacing: .1em;
    z-index: 1;
    position: relative;


    @media screen and (min-width: $query__small) {
        font-size: 1.5rem;
    }
}

.study-header-tagline {
    font-size: .95rem;
    color: #fff;
    word-spacing: .1em;
    z-index: 1;
    position: relative;
    margin: 2em .25em 0;

    @media screen and (min-width: $query__small) {
        font-size: 1rem;
        width: 50%;
    }
}

//Contact Snippet Pages
.page-contact {
    background: url('https://kelcherpromotions.com/wp-content/uploads/2018/04/contact-kelcher-today.jpg' ) no-repeat center center;
    background-size: cover;
    @include dark-background( rgba( 0,0,0,.7 ) );
    color: #fff;
    text-align: center;
    position: relative;
    padding: 4em 1em;

    h4 {
        margin: 0 0 2em;
        font-weight: 400;
        font-size: .95rem;
        position: relative;
        z-index: 1;
    }

    a {
        position: relative;
        z-index: 1;
        background: $color__site-green;
        padding: 8px 20px;
        text-decoration: none;
        color: #fff;
        font-size: .75rem;
        font-family: $font__sans;
        border: 2px solid $color__site-green;
        transition: background .25s linear, border-color .25s linear;

        @media screen and (min-width: $query__small) {
            padding: 12px 25px;
            font-size: .8rem;
        }

        &:hover,
        &:focus {
            background: transparent;
            border-color: $color__site-green;
            color: $color__site-green;
        }
    }

    @media screen and (min-width: 500px) {
        padding: 4em 0;

        h4 {
            font-size: 1.1rem;
        }
    }

    @media screen and (min-width: $query__tablet) {
        h4 {
            font-size: 1.3rem;
        }
    }
}

//Individual Page Into
.page-intro {
    text-align: center;
    margin: 4em auto;
    width: 90%;

    h2 {
        margin: 0;
        font-size: 1.5rem;
    }

    p {
        margin: 2em 0 0;
        word-spacing: 0.1em;
        color: #777;
    }

    @media screen and (min-width: 700px) {
        margin: 8em auto;
        
        h2 {
            font-size: 2.5rem;
        }
    }

    @media screen and (min-width: 1025px) {
        width: 55%;
    }
}

//Service Pages
.service-pages-intro {
    display: block;
    margin: 3em auto;

    @media screen and (min-width: $query__small) {
        max-width: 1150px;
        display: flex;
        margin: 4em auto;
    }

    @media screen and (max-width: $query__medium) {
        align-items: center;
    }

    .service-pages-intro-text {
        padding: 0 1em;

        @media screen and (min-width: $query__small) {
            flex: 1;
        }

        @media screen and (min-width: $query__medium) {
            padding: 0 2em 0 1em;
        }

        h3 {
            font-size: 1.25rem;
            margin: 1em 0;
            @include header-uline(#c3c3c3);

            &:after {
                height: 3.5px;
                margin: 0;
            }

            @media screen and (min-width: $query__small) {
                font-size: 1.75rem;
            }
        }

        p {
            color: #777;
            line-height: 1.7;
            word-spacing: .01em;
        }
    }

    .service-pages-intro-img {

        @media screen and (min-width: $query__small) {
            flex: 1;
        }

        img {
            display: block;
        }
    }
}